body {
  background-color: #f5f5f5;
  margin: 0;
  padding: 0; }

.container {
  max-width: 840px;
  background-color: #fff;
  margin: 0 -40px;
  padding: 40px;
  margin: 0 auto;
  box-sizing: border-box;
  border-radius: 4px; }

#printable {
  width: 210mm;
