section > h2 {
  text-align: center; }
  section > h2:after {
    content: " ";
    display: flex;
    margin: 0 auto;
    padding-top: .55rem;
    width: 100%;
    height: 0;
    border-bottom: solid 2px #000; }

nav {
  width: 100%;
  background: #00796B;
  margin-bottom: 40px;
  position: -webkit-sticky;
  position: sticky;
  top: 0;
  z-index: 1; }
  nav .siteNav {
    list-style-type: none;
    padding: 0;
    display: flex;
    flex-direction: row;
    height: 50px;
    vertical-align: middle;
    align-items: center;
    max-width: 210mm;
    margin: 0 auto; }
    nav .siteNav li {
      margin: 0 20px; }
      nav .siteNav li:first-child {
        margin-left: 0; }
      nav .siteNav li:last-child {
        margin-right: 0; }
      nav .siteNav li.export {
        margin-left: auto; }
        nav .siteNav li.export a {
          cursor: pointer; }
        nav .siteNav li.export svg {
          margin-right: 10px; }
      nav .siteNav li a {
        color: #FFF;
        text-decoration: none;
        position: relative; }
        nav .siteNav li a:after {
          content: "";
          position: absolute;
          width: 100%;
          height: 2px;
          bottom: -5px;
          left: 0;
          background-color: #FFF;
          visibility: hidden;
          -webkit-transform: scaleX(0);
                  transform: scaleX(0);
          -webkit-transition: all .3s ease-in-out;
          transition: all .3s ease-in-out; }
        nav .siteNav li a:hover:after {
          visibility: visible;
          -webkit-transform: scaleX(1);
                  transform: scaleX(1); }

.profile h3 {
  margin-top: 0; }
  .profile h3:after {
    content: " ";
    display: flex;
    margin: 0 auto;
    padding-top: .55rem;
    width: 100%;
    height: 0;
    border-bottom: solid 2px #000; }

.profile .intro {
  display: flex;
